import { HttpClient } from "@angular/common/http";
import { Component, ElementRef, OnInit, ViewChild } from "@angular/core";
import { NgForm } from "@angular/forms";
import { LoadingController, ModalController } from "@ionic/angular";
import { API } from "src/environments/environment";
import { SuccessModalComponent } from "./success-modal/success-modal.component";
import axios from "axios";
import { Router } from "@angular/router";
import { StatusBar } from "@capacitor/status-bar";

@Component({
	selector: "app-register",
	templateUrl: "./register.page.html",
	styleUrls: ["./register.page.scss"],
})
export class RegisterPage implements OnInit {
	@ViewChild("passwordEyeRegister", { read: ElementRef })
	passwordEye: ElementRef;
	passwordTypeInput = "password";
	countryCodes = [];
	errors: any = {
		name: [],
		last_name: [],
		email: [],
		password: [],
		phone1: [],
	};

	constructor(
		private modalController: ModalController,
		private http: HttpClient,
		private lc: LoadingController,
		private router: Router
	) {}

	ngOnInit() {
		StatusBar.hide();
		this.getCountryCodes();
		this.loadFlags = this.loadFlags.bind(this);
	}

	async onRegister(form: NgForm) {
		console.log(form);
		if (!form.valid) {
			return;
		}
		let name = form.value.name;
		let wName = name.split(" ");
		name = wName[0];
		let l_name = wName[1];
		if (wName[2] !== undefined) {
			l_name += " " + wName[2];
		}
		const email = form.value.email;
		const password = form.value.password;
		const phone1 = `${form.value.countryCode} ${form.value.phone1}`;
		const confirm_password = form.value.confirm_password;

		if (password !== confirm_password) {
			this.errors.password = ["Las contraseñas no coinciden"];
			return;
		}

		const loader = await this.lc.create({
			message: "Registrando tu usuario...",
		});

		loader.present();

		const body = {
			name,
			last_name: l_name,
			email,
			phone1,
			password,
		};

		try {
			let response = await axios.post(`${API}/auth/register`, body, {
				headers: {
					"Content-Type": "application/json",
					"X-Requested-With": "XMLHttpRequest",
				},
			});
			const { success, data } = response.data;
			loader.dismiss();

			if (success) {
				this.clearErrors();
				form.reset();
				// modal for succes
				const successModal = await this.modalController.create({
					component: SuccessModalComponent,
					cssClass: "modalSuccess",
				});
				successModal.present();
			} else {
				if (data.length) {
					this.errors.email = data;
				}
			}
		} catch (error) {
			const { response } = error;
			if (response) {
				const { errors } = response.data;

				this.errors.name = [];
				if (errors.name !== undefined) {
					this.errors.name = errors.name;
				}
				this.errors.last_name = [];
				if (errors.last_name !== undefined) {
					this.errors.last_name = errors.last_name;
				}
				this.errors.email = [];
				if (errors.email !== undefined) {
					this.errors.email = errors.email;
				}
				this.errors.password = [];
				if (errors.password !== undefined) {
					this.errors.password = errors.password;
				}
			}

			loader.dismiss();
		}
	}

	async getCountryCodes() {
		try {
			let response = await axios.get(
				"https://restcountries.eu/rest/v2/region/Americas"
			);
			this.countryCodes = response.data;
		} catch (error) {
			console.log(error);
		}
	}

	clearErrors() {
		this.errors.name = [];
		this.errors.last_name = [];
		this.errors.email = [];
		this.errors.password = [];
	}

	goToTerms() {
		this.router.navigate(["/terms"]);
	}

	goToPrivacy() {
		this.router.navigate(["/privacy"]);
	}
	togglePasswordMode() {
		this.passwordTypeInput =
			this.passwordTypeInput === "text" ? "password" : "text";
	}

	loadFlags() {
		setTimeout(() => {
			let radios = document.getElementsByClassName(
				"alert-radio-label"
			);
			for (let index = 0; index < radios.length; index++) {
				let element = radios[index];
				element.innerHTML = `<img class="country-image" style="width: 30px;height:16px;" src="${this.countryCodes[index].flag}" /> ${element.innerHTML}`;
			}
		}, 100);
	}
}
